import request from '../request';
import store from '../../store/index';

//
export function setUserFavorite(id: string) {
  return request({
    url: '/user/favorite',
    method: 'post',
    data: {
      id
    },
    headers: {
      // @ts-ignore
      authorization: store.getState().getIn(['loginReducer', 'login', 'userMsg']).token
    }
  });
}

//
export function getUserFavorite(offset: number, limit: number) {
  return request({
    url: '/user/favorite',
    params: {
      offset,
      limit
    },
    headers: {
      // @ts-ignore
      authorization: store.getState().getIn(['loginReducer', 'login', 'userMsg']).token
    }
  });
}

//
export function cancelFavorite(songId: string) {
  return request({
    url: '/user/favorite/cancel',
    method: 'post',
    data: {
      songId: songId
    },
    headers: {
      // @ts-ignore
      authorization: store.getState().getIn(['loginReducer', 'login', 'userMsg']).token
    }
  });
}

//
export function getUserSub(url: string, offset: number, limit: number) {
  return request({
    url: `/sub/user/${url}`,
    params: {
      offset,
      limit
    },
    headers: {
      // @ts-ignore
      authorization: store.getState().getIn(['loginReducer', 'login', 'userMsg']).token
    }
  });
}

//
export function getUserDetail() {
  return request({
    url: '/user/detail',
    headers: {
      // @ts-ignore
      authorization: store.getState().getIn(['loginReducer', 'login', 'userMsg']).token
    }
  });
}

//
export function uploadAvatar(formData: FormData) {
  return request({
    url: '/user/upload/avatar',
    method: 'post',
    headers: {
      // @ts-ignore
      authorization: store.getState().getIn(['loginReducer', 'login', 'userMsg']).token,
      'Content-Type': 'multipart/form-data'
    },
    data: formData
  });
}
